import { useContext, useRef, useEffect } from 'react'
import { Context } from '.'

const SharePanel = () => {
  const { topic, setActionBar } = useContext(Context)
  useEffect(() => {
    const handle = e => {
      if(shareRef.current.parentNode.contains(e.target)) {

      }
      else {
        setActionBar(null)
      }
    }
    document.addEventListener('click', handle, true)
    return () => {
      document.removeEventListener('click', handle, true)
    }
  }, [])
  const shareRef = useRef()
  return <div ref={ shareRef } style={ { textAlign: 'center' } }>
    <h2><i>Share to</i></h2>
    <div>
      <a className="link with-big-facebook" data-share="facebook" onClick={ () => setActionBar(null) } />
      <a className="link with-big-whatsapp" data-share="whatsapp" onClick={ () => setActionBar(null) }/>
    </div>
  </div>
}

export default SharePanel